  position: absolute;
  top: 0;
  left: 0;
}

.images-thumb {
  overflow: hidden;
  width: 1920px;
  height: 424px;
}
.images-thumb--services {
  width: 575px;
  height: 420px;
}
.images-thumb--weoffer {
  grid-column: 7 span;
  width: 705px;
  height: 440px;
}
.images-thumb--avatar {
  border-radius: 100%;
  width: 72px;
  height: 72px;
  margin-bottom: 25px;
}
.images-thumb--decisions {
  grid-column: 5 span;
  width: 495px;
  height: 550px;
}
.images-thumb--history {
  width: 810px;
  height: 450px;
  margin-bottom: 36px;
}
.images-thumb--team {
  width: 285px;
  height: 340px;
}
.images-thumb--news {
  width: 600px;
  height: 306px;
}
.images-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.splide {
  margin-bottom: 10px;
}
.splide__pagination {
  bottom: 4%;
}
.splide__pagination li:not(:last-child) {
  margin-right: 15px;
}
.splide:not(:last-child) {
  margin-right: 15px;
}
.splide__arrow {
  width: 51px;
  height: 51px;
  background: rgba(255, 255, 255, 0.5);
}
.splide__arrow svg {
  width: 28px;
  height: 18px;
}
.splide__arrow--next {
  right: 2.4%;
}
.splide__arrow--prev {
  left: 2.4%;
}
.splide__pagination__page {
  position: relative;
  display: inline-block;
  margin: 0;
  padding: 0;
  width: 9px;
  height: 9px;
  background: #FFF;
  border: 0;
  border-radius: 50%;
  opacity: 1;
  transition: transform 0.2s linear;
}
.splide__pagination__page.is-active {
  background: #9D0000;
  transform: scale(1.4);
  z-index: 1;
}

.hero-links__list {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: center;
}
.hero-links__item {
  flex-basis: calc((100% - 20px) / 3);
}
.hero-links__link {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  transition-duration: 450ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.hero-links__link::after {
  position: absolute;
  inset: 0;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
}
@media (any-hover: hover) {
  .hero-links__link:hover {
    box-shadow: -7px 0 22px 0 rgba(0, 0, 0, 0.35);
  }
  .hero-links__link:hover .hero-links__link-bg {
    scale: 1.5;
  }
}
.hero-links__link-bg {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  background-position: top left;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
  transition-duration: 450ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.hero-links__title {
  position: relative;
  z-index: 3;
  padding-top: 103px;
  padding-bottom: 104px;
}

.title-h3 {
  font-weight: 600;
  font-size: clamp(1.125rem, 0.95rem + 0.88vw, 2rem);
  letter-spacing: 0.03em;
  text-align: center;
  color: #FFF;
}