.search {
  // max-width: 512px;
  // width: 100%;
  width: clamp(12.5rem, -9.786rem + 34.82vw, 32rem);
  display: flex;
  align-items: center;

  &__input {
    position: relative;
    width: 100%;

    padding-top: 18px;
    padding-right: 10px;
    padding-bottom: 10px;
    // font-family: var(--font-family);
    //   font-weight: 400;
    font-size: 18px;
    color: rgba(#fff, .5);
    border: none;
    border-bottom: 1px solid rgba(#fff, .75);
    background-color: transparent;
  }

  &__btn {
    margin-left: -17px;
    position: relative;
    z-index: 3;
    width: 17px;
    height: 17px;
    background: url(../images/icons/search.svg) center center /cover no-repeat;
  }
}